<template>
  <div class="wrapper">
    <div>第{{n}}手</div>
    <dir class="chess">
      <div class="row">
        <Cell v-on:click="onClickCell(0,$event)" v-bind:n="n" />
        <Cell v-on:click="onClickCell(1,$event)" v-bind:n="n" />
        <Cell v-on:click="onClickCell(2,$event)" v-bind:n="n" />
        <!-- v-on可以替换成@；v-bind可以不写 -->
      </div>
      <div class="row">
        <Cell v-on:click="onClickCell(3,$event)" v-bind:n="n" />
        <Cell v-on:click="onClickCell(4,$event)" v-bind:n="n" />
        <Cell v-on:click="onClickCell(5,$event)" v-bind:n="n" />
      </div>
      <div class="row">
        <Cell v-on:click="onClickCell(6,$event)" v-bind:n="n" />
        <Cell v-on:click="onClickCell(7,$event)" v-bind:n="n" />
        <Cell v-on:click="onClickCell(8,$event)" v-bind:n="n" />
      </div>
    </dir>
    <!-- <div>{{map}}</div> -->
    <!-- <dir>{{result}}</dir> -->
    <div>结果：{{result==null ? '胜负未分' : `胜方为 ${result}`}}</div>
  </div>
</template>

<script>
import Cell from "./Cell";

export default {
  components: { Cell },
  data() {
    return {
      n: 0,
      map: [
        [null, null, null],
        [null, null, null],
        [null, null, null]
      ],
      result: null
      //没赢
    };
  },
  methods: {
    onClickCell(i, text) {
      console.log(`${i}号内容被点击了，内容是：${text}`);
      this.map[Math.floor(i / 3)][i % 3] = text;
      this.n = this.n + 1;
      this.tell();
    },
    tell() {
      for (let i = 0; i <= 2; i++) {
        if (
          this.map[i][0] != null &&
          this.map[i][0] == this.map[i][1] &&
          this.map[i][1] == this.map[i][2]
        ) {
          this.result = this.map[i][0]; //赢了
        }
      }
      for (let j = 0; j <= 2; j++) {
        if (
          this.map[0][j] != null &&
          this.map[0][j] == this.map[1][j] &&
          this.map[1][j] == this.map[2][j]
        ) {
          this.result = this.map[0][j];
        }
      }
      if (
        this.map[0][0] != null &&
        this.map[0][0] == this.map[1][1] &&
        this.map[1][1] == this.map[2][2]
      ) {
        this.result = this.map[0][0];
      }
      if (
        this.map[0][2] != null &&
        this.map[0][2] == this.map[1][1] &&
        this.map[1][1] == this.map[2][0]
      ) {
        this.result = this.map[0][2];
      }
    }
  }
};
</script>

<style>
.row {
  display: flex;
}
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* flex-direction: column;使 第几手  在上面  */
}
</style>
