<template>
  <div>
    <!-- {{n}} -->
    <div class="cell" v-on:click="onClickSelf">
      <div v-if="a">{{text}}</div>
      <div v-else></div>
    </div>
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";

export default {
  props: ["n"],
  data() {
    //ES6语法
    return { a: false, text: "" };
    // a默认为false,所以一开始显示为空
  },

  methods: {
    onClickSelf() {
      if (this.text != "") {
        return;
      }
      this.a = true;
      this.text = this.n % 2 == 0 ? "x" : "o";
      // text如果是偶数就是x，如果是奇数就是o
      this.$emit("click", this.text);
      //   emit 说明已经点击
    }
  }
};
</script>

<style>
.cell {
  border: 1px solid black;
  height: 100px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 80px;
}
</style>
